import {reduxForm} from "redux-form";
import {createField, Input} from "../common/FormsControls/FormsControl";
import {minLengthCreator, requiredField} from "../../utils/validators/validators";
import {connect} from "react-redux";
import {login, logout} from "../../redux/auth-reducer";
import {Navigate} from "react-router-dom";

import styles from './Login.module.css';

const minLengthCreator2 = minLengthCreator(2)

const LoginForm = ({handleSubmit, error}) => {

	return (
		<form onSubmit={handleSubmit}>
			{createField('email', 'Login', [requiredField, minLengthCreator2], Input, 'text')}
			{createField('password', 'Password', [requiredField], Input, 'password')}
			{createField('rememberMe', null, null, Input, "checkbox", 'remember me')}
			{error && <div className={styles.form_summary_error}>
				{error}
			</div>}
			<div>
				<button>Login</button>
			</div>
		</form>
	)
}

const LoginReduxForm = reduxForm({
	form: 'login'
})(LoginForm)

const Login = ({login, isAuth}) => {
	const onSubmit = (formData) => {
		login(formData.email, formData.password, formData.rememberMe)
	}

	if (isAuth) {
		return <Navigate replace to={'/profile'}/>
	}
	return (
		<div>
			<h1>Login</h1>
			<LoginReduxForm onSubmit={onSubmit}/>
		</div>
	)
}
const mapStateToProps = (state) => ({
	isAuth: state.auth.isAuth
})

export default connect(mapStateToProps, {
	login,
	logout
})(Login);